import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import { Button, FormControl, Table } from 'react-bootstrap';
import axios from 'axios';

export default function SitesDiv({setModalData}) {
    const {userData, globals, apiCall, getUserData} = useContext(GlobalContext);
    const [userInputs, setUserInputs] = useState({
        username: '',
        password: ''
    });

    const [sitesData, setSitesData] = useState({
        sites: [],
        // password: ''
    });

    async function getSites(){
        let response = await apiCall(globals.varNames.apiPath + 'user/getAllSites', {});
        
        // let response = await apiCall(globals.varNames.apiPath + 'user/login', data);
        console.log('response: ', response);
        if(response.status == 1){
            setSitesData({...sitesData, sites: response.payload});
        }
    }

    // async function getSosyalSites(){
    //     let response = await apiCall(globals.varNames.apiPath + 'user/getSosyalSites', {});
        
    //     // let response = await apiCall(globals.varNames.apiPath + 'user/login', data);
    //     console.log('response: ', response);
    //     if(response.status == 1){
            
    //     }
    // }

    useEffect(() => {
        getSites();
        // getSosyalSites();
    }, []);
    
    return (
        <div>
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Name</th>
                        <th>Password</th>
                        <th>Username</th>
                    </tr>
                </thead>
                <tbody>
                    {sitesData.sites.map((site, idx) => 
                        // <ItemList openUploadModal={()=>{setModalsData({modalName: 'UploadPhoto', data: item});}} key={item.id} item = {item} />
                        <tr onClick={()=>{setModalData({isOpen: true, data: site})}}>
                            <td>{site.id}</td>
                            <td>{site.name}</td>
                            <td>{site.api}</td>
                            <td>@mdo</td>
                        </tr>
                    )}
                </tbody>
                
                
            </Table>
            {/* <Button onClick={()=>{setModalData({isOpen: true, })}} variant="primary">Login</Button> */}
        </div>
    );
}


