import React, { useState, useContext, useRef } from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import Sosyal from '../Views/Sosyal';
import Wallet from '../Views/Wallet';
import Sites from '../Views/Sites';
import Verify from '../Views/Verify';
import { Button, FormControl, Table, Modal, Nav, Navbar, NavDropdown, Form } from 'react-bootstrap';

export default function InsidePages() {
    const {userData, globals, apiCall, getUserData, forceUpdate} = useContext(GlobalContext);

    function navPage(page){
        userData.currentPage.back = page;
        forceUpdate();
    }

    let page = <Sosyal/>;

    if(userData.currentPage.back == 'Wallet'){
        page = <Wallet/>;
    }else if(userData.currentPage.back == 'Sites'){
        page = <Sites/>
    }else if(userData.currentPage.back == 'Verify'){
        page = <Verify/>
    }

    return (
        <div>
            <Navbar bg="light" expand="lg">
                <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                    <Nav.Link href="#home">Home</Nav.Link>
                    <Nav.Link onClick={()=>navPage('Sites')} href="#link">Sites</Nav.Link>
                    <Nav.Link onClick={()=>navPage('Sosyal')} href="#link">Sosyal</Nav.Link>
                    <Nav.Link onClick={()=>navPage('Wallet')} href="#link">Wallet</Nav.Link>
                    <Nav.Link onClick={()=>navPage('Verify')} href="#link">Verify</Nav.Link>
                    <Nav.Link href="#link">Logout</Nav.Link>
                    {/* <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                    </NavDropdown> */}
                    </Nav>
                    {/* <Form inline>
                    <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                    <Button variant="outline-success">Search</Button>
                    </Form> */}
                </Navbar.Collapse>
            </Navbar>
            {page}
        </div> 
    );
}

