import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import InsidePages from './InsidePages';
import OutsidePages from './OutsidePages';


export default function Pages() {
    console.log('PAGES');
    const {userData, appData, globals, getUserData} = useContext(GlobalContext);
    const [fontLoaded, setFontLoaded] = useState(false);
    // console.disableYellowBox = true;
	function initiateVariables(){
        let beginPath = globals.varNames.beginPathProduction;
        let port = '';
        if(!userData.production){
            beginPath = globals.varNames.beginPath;
            port = ':' + globals.varNames.port;
        }
        // globals.varNames.apiPath = beginPath + ':' + globals.varNames.shappersPort + '/api/';
        // globals.varNames.blagsApiPath = beginPath + ':' + globals.varNames.blagsPort + '/api/';
        // globals.varNames.blagsServerPath = beginPath + ':' + globals.varNames.shappersPort;
        // globals.varNames.imgPath = beginPath + ':' + globals.varNames.shappersPort + '/profile/';
        // globals.varNames.itemsImgPath = beginPath + ':' + globals.varNames.shappersPort + '/items/';
        globals.varNames.apiPath = beginPath + port + '/api/';
        appData.siteID = globals.varNames.siteID;
        // if(!userData.production){
        //     globals.varNames.apiPath = globals.varNames.devApi;
        // }    
    }

    async function getUserToken(){
        console.log('get user token');
        
        let value = await localStorage.getItem('userData');
        let userDataStorage = JSON.parse(value); 
        
        if(userDataStorage != null){
            console.log('userdata storage: ', userDataStorage);
            // userData.token = userDataStorage.token;
            getUserData(userDataStorage.token, userDataStorage.sitePass);
            
            
            
        }   
    }

    useEffect(() => {
        // Update the document title using the browser API
        initiateVariables();
        
        getUserToken();
        // await getUserToken(userData, getUserData);
    }, []);
    
    let app = <OutsidePages/>;
    if(userData.loggedIn){
        app = <InsidePages/>;
    }
    return (
        app

        
    );
}



// export default Pages;