import React, {createContext, useReducer, useState} from 'react';

import AppReducer from './AppReducer';
import InitialState from './InitialState';
import axios from 'axios';



const initialState = InitialState;




export const GlobalContext = createContext(initialState);

export const GlobalProvider = ({children}) => {
    console.log('GLOBAL PROVIDER');
    const [state, dispatch] = useReducer(AppReducer, initialState);

    async function apiCall(url, data){
        // console.log('api call');
        // console.log('url: ', url);
        // console.log('token: ', state.userData.token);
        let response = await axios({
            method: 'post',
            url: url,
            data: data,
            headers: {
                'x-access-token': state.userData.token,
                'x-password': state.userData.sitePass
            }    
        })
        // console.log('response: ', response);
        return response.data;
    }

    async function getUserData(token, sitePass){
        console.log('get user data');
        console.log('api path: ', state.globals.varNames.apiPath);
        // console.log('token: ', state.userData.token);
        state.userData.token = token;
        state.userData.sitePass = sitePass;
        localStorage.setItem('userData', JSON.stringify({token: state.userData.token, sitePass: sitePass}));
        var response = await apiCall(state.globals.varNames.apiPath + 'user/getUserData', {});  
       
        console.log('response: ', response);
        if(response.status == 1){
            // initiateUserData(response.payload.userData);
            // setVariables(response.payload);
            // initiateGlobals(response.payload.globals);
            // var responseSosyal = await apiCall(state.globals.varNames.blagsApiPath + 'user/getUserDataOnly', {});
            // console.log('response sosyal: ', responseSosyal);
            // if(responseSosyal.status == 1){
            //     initiateSosyalData(responseSosyal.payload);
            // }
            state.userData.loggedIn = true;
            state.userData.currentPage.back = 'profile';
            // connectToSocket();
            forceUpdate();
            // dispatch({
            //     type: 'UPDATE_USER_DATA',
            //     payload: 'username'
            // });
        }
        // return response;
    }

    

    function forceUpdate(){
        dispatch({
            type: 'UPDATE_USER_DATA',
            payload: 'username'
        });   
    }

    function dateToStr(payload) { 
        let timestampDate = new Date(payload * 1000);
        let hours = timestampDate.getHours();
        let minutes = timestampDate.getMinutes();
        let seconds = timestampDate.getSeconds();
        let year = timestampDate.getFullYear();
        let month = timestampDate.getMonth() + 1;
        let date = timestampDate.getDate();  
        
        return month + "/" + date + "/" + year + " " + hours + ":" + minutes + ":" + seconds;     
    }

    function changePage(page){
        console.log('initiate user data');
        state.userData.currentPage.back = page;
        forceUpdate();
        // dispatch({
        //     type: 'UPDATE_USER_DATA',
        //     payload: 'username'
        // });
        
    }

    function logout(){
        state.userData.loggedIn = false;
         
        state.userData.currentPage.front = 'login';    
        forceUpdate();  
    }

    return (<GlobalContext.Provider value = {{
        
        userData: state.userData,
        globals: state.globals,
        appData: state.appData,
        getUserData,
        apiCall,
        changePage,
        forceUpdate,
        dateToStr,
        logout
        
    }}>
        {children}
    </GlobalContext.Provider>)
}