import React, {useState, useContext} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import { Button, FormControl } from 'react-bootstrap';
import axios from 'axios';

export default function Login() {
    const {userData, globals, apiCall, getUserData} = useContext(GlobalContext);
    const [userInputs, setUserInputs] = useState({
        username: '',
        password: '',
        sitePass: ''
    });

    async function login(){
         
        let response = await axios({
            method: 'post',
            url: globals.varNames.apiPath + 'user/login',
            data: {
                userData: JSON.stringify(userInputs),
                sitePass: userInputs.sitePass     
            },
                
        })
        // let response = await apiCall(globals.varNames.apiPath + 'user/login', data);
        console.log('response: ', response);
        if(response.data.status == 1){
            getUserData(response.data.payload, userInputs.sitePass);
        }
    }
    
    return (
        <div>
            <FormControl
                placeholder="Username"
                aria-label="Username"
                aria-describedby="basic-addon1"
                onChange={(e) => {setUserInputs({...userInputs, username: e.target.value})}}
            />
            <FormControl
                placeholder="Password"
                aria-label="Username"
                aria-describedby="basic-addon1"
                onChange={(e) => {setUserInputs({...userInputs, password: e.target.value})}}
            />
            <FormControl
                placeholder="Site Pass"
                aria-label="Username"
                aria-describedby="basic-addon1"
                onChange={(e) => {setUserInputs({...userInputs, sitePass: e.target.value})}}
            />
            <Button onClick={login} variant="primary">Login</Button>
        </div>
    );
}