import React, {useState, useContext} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import { Button, FormControl } from 'react-bootstrap';
import axios from 'axios';
import SitesDiv from '../Components/SitesDiv';
import SiteModal from '../Components/SiteModal';

export default function Wallet() {
    const {userData, globals, apiCall, getUserData} = useContext(GlobalContext);
    const [password, setPassword] = useState('');
    const [dwInputs, setDWInputs] = useState({
        amount: 0,
        address: '',
        type: true
    });
    
    async function depositWithdraw(){
        let type = 'deposit';
        if(!dwInputs.type){
            type = 'withdraw';
        }
        let data = {
            amount: dwInputs.amount,
            address: dwInputs.address,
            type: type
        }
        console.log('data: ', data);
        let response = await apiCall(globals.varNames.apiPath + 'user/depositWithdraw', data);
        
        // let response = await apiCall(globals.varNames.apiPath + 'user/login', data);
        console.log('response: ', response);
        if(response.status == 1){
           
        }  
    }
    
    let label = 'Deposit';
    if(dwInputs.type == false){
        label = 'Withdraw';
    }
    
    return (
        <div>
            <Button onClick={()=>setDWInputs({...dwInputs, type: !dwInputs.type})} variant="link">{label}</Button>
            <FormControl
                placeholder="Address"
                aria-label="Username"
                aria-describedby="basic-addon1"
                onChange={(e) => {setDWInputs({...dwInputs, address: e.target.value})}}
            />
            <FormControl
                placeholder="Amount"
                aria-label="Username"
                aria-describedby="basic-addon1"
                onChange={(e) => {setDWInputs({...dwInputs, amount: e.target.value})}}
            />
            
            <Button onClick={depositWithdraw} variant="primary">Ok</Button>
        </div>
    );
}