import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import { Button, FormControl, Table, Modal } from 'react-bootstrap';
import axios from 'axios';

export default function SiteModal({setModalData, modalData}) {
    const {userData, globals, apiCall, getUserData} = useContext(GlobalContext);
    const [password, setPassword] = useState('');
    async function changePassword(){
        let data = {
            siteID: modalData.data.id,
            password: password
        }
        let response = await apiCall(globals.varNames.apiPath + 'user/changeSosyalPassword', data);
        
        // let response = await apiCall(globals.varNames.apiPath + 'user/login', data);
        console.log('response: ', response);
        if(response.status == 1){
           
        }  
    }
    

    
    
    return (
        <Modal show={modalData.isOpen} onHide={()=>setModalData({...modalData, isOpen: false})}>
            <Modal.Header closeButton>
                <Modal.Title>Modal title</Modal.Title>
            </Modal.Header>

            <Modal.Body>
                <FormControl
                    placeholder="Password"
                    aria-label="Username"
                    aria-describedby="basic-addon1"
                    onChange={(e) => {setPassword(e.target.value)}}
                />
                
                <Button onClick={changePassword} variant="primary">Login</Button>
            </Modal.Body>

            <Modal.Footer>
                <Button variant="secondary">Close</Button>
                <Button variant="primary">Save changes</Button>
            </Modal.Footer>
        </Modal>
    );
}