import React, {useState, useContext} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import { Button, FormControl } from 'react-bootstrap';
import axios from 'axios';
import SitesDiv from '../Components/SitesDiv';
import SiteModal from '../Components/SiteModal';

export default function Sites() {
    const {userData, globals, apiCall, getUserData} = useContext(GlobalContext);
    const [modalData, setModalData] = useState({
        isOpen: false,
        data: null
    })
    
    

    let modal = null;
    if(modalData){
        modal = <SiteModal setModalData={setModalData}/>
    }
    return (
        <div>
            <SiteModal setModalData={setModalData} modalData={modalData}/>
            
            <SitesDiv setModalData={setModalData}/>
            
        </div>
    );
}