import React, {useState, useContext, useEffect} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import { Button, FormControl, Table } from 'react-bootstrap';
import axios from 'axios';

export default function SitesDiv({setModalData}) {
    const {userData, globals, apiCall, getUserData} = useContext(GlobalContext);
    const [userInputs, setUserInputs] = useState({
        username: '',
        password: ''
    });

    const [usersData, setUsersData] = useState({
        users: [],
        // password: ''
    });

    async function getUsers(){
        let response = await apiCall(globals.varNames.apiPath + 'user/getSosyalSites', {});
        
        // let response = await apiCall(globals.varNames.apiPath + 'user/login', data);
        console.log('response: ', response);
        if(response.status == 1){
            setUsersData({...userData, users: response.payload});
        }
    }

    

    useEffect(() => {
        getUsers();
        // getSosyalSites();
    }, []);
    
    return (
        <div>
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Username</th>
                        <th>Password</th>
                        <th>Name</th>
                    </tr>
                </thead>
                <tbody>
                    {usersData.users.map((user, idx) => 
                        // <ItemList openUploadModal={()=>{setModalsData({modalName: 'UploadPhoto', data: item});}} key={item.id} item = {item} />
                        <tr key={idx} onClick={()=>{setModalData({isOpen: true, data: user})}}>
                            <td>{user.id}</td>
                            <td>{user.username}</td>
                            <td>{user.password}</td>
                            <td>@mdo</td>
                        </tr>
                    )}
                </tbody>
                
                
            </Table>
            {/* <Button onClick={()=>{setModalData({isOpen: true, })}} variant="primary">Login</Button> */}
        </div>
    );
}