import React, {useState, useContext} from 'react'; 
import {GlobalContext} from '../Context/GlobalProvider';
import { Button, FormControl } from 'react-bootstrap';
import axios from 'axios';
import SitesDiv from '../Components/SitesDiv';
import SiteModal from '../Components/SiteModal';

export default function Verify() {
    const {userData, globals, apiCall, getUserData} = useContext(GlobalContext);
    const [password, setPassword] = useState('');
    const [verifyInputs, setVerifyInputs] = useState({
        username: '',
        userID: 0,
        // type: true
    });
    
    async function getUser(){
        

        let data = {
            username: verifyInputs.username,
            
        }

        console.log('data: ', data);
        let response = await apiCall(globals.varNames.apiPath + 'user/getVerifiedData', data);
        
        // let response = await apiCall(globals.varNames.apiPath + 'user/login', data);
        console.log('response: ', response);
        if(response.status == 1){
            setVerifyInputs({...verifyInputs, userID: response.payload.userid});    
        }  
    }
    
    async function verifyUser(){
        

        let data = {
            userID: verifyInputs.userID,
            
        }

        console.log('data: ', data);
        let response = await apiCall(globals.varNames.apiPath + 'user/verifyUser', data);
        
        // let response = await apiCall(globals.varNames.apiPath + 'user/login', data);
        console.log('response: ', response);
        if(response.status == 1){
            setVerifyInputs({...verifyInputs, userID: response.payload.userid});    
        }  
    }
    
    return (
        <div>
            {/* <Button onClick={()=>setDWInputs({...dwInputs, type: !dwInputs.type})} variant="link">{label}</Button> */}
            <FormControl
                placeholder="Username"
                aria-label="Username"
                aria-describedby="basic-addon1"
                onChange={(e) => {setVerifyInputs({...verifyInputs, username: e.target.value})}}
            />
            {/* <FormControl
                placeholder="Amount"
                aria-label="Username"
                aria-describedby="basic-addon1"
                onChange={(e) => {setDWInputs({...dwInputs, amount: e.target.value})}}
            /> */}
            
            <Button onClick={getUser} variant="primary">Get User</Button>
            <Button onClick={verifyUser} variant="primary">Verify</Button>
        </div>
    );
}